        margin-bottom: 70px;
    }
    .CRResources{
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
    }
    .CTRICRResource{
        padding: 0 20px 0 20px;
        margin: 30px 0 140px 0;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        text-align: center;
        align-items: center;
        justify-content: center;
