        font-weight: 500;
    }
    .CRPDescriptor {
        margin: 0 0 50px 0;
        color: #cbcbcb;
        font-family: Arial,'Times New Roman';
        font-size: 1.6rem;
        font-weight: 400;
    }
    .TopScroll {
        display: none;
    }
    .CRTextBox{
        text-align: left;
        max-width: 600px;
    }
    .CRCTA{
        padding: 10px;
        border: 2px solid #e76203;
        border-radius: 10px;
        background: #e76203;
        font-size: 1.4rem;
        font-family: Helvetica, Arial;
        font-weight: 700;
        color:black;
        display: inline;
        cursor: pointer;
    }
    .MobileScrollUp{
        color: white;
        font-size: 1.5rem;
        font-family: Arial, 'Times New Roman' ;
        font-weight: 400;
    }
    .CRResourceImage {
        display: none;
    }
    .RightScroll{
        display: none;
    }
}